	module.exports = {"data":{"markdownRemark":{"html":"<p>しまった、気付くと週刊のはずが2週おきに……更新もまばら、一体私は何やってたのでしょう。ｗ ところで、今回<em>[Internet Archive</em>](<a href=\"http://www.archive.org/\">http://www.archive.org/</a>)と言うサイトを見つけました。このサイトを開くとまず現れてくるエディットボックスに行きたいURIを指定すると、なんと指定したサイトの過去を見ることが出来ます。例えば<a href=\"http://www.nttdocomo.co.jp/\">http://www.nttdocomo.co.jp/</a>と入力してみてくださいな。すると……  <a href=\"http://web.archive.org/web/19961112145649/http://www.nttdocomo.co.jp/\">こうなります</a>。うわーD201が……当時はケータイとインターネットは無縁同様だったんですね。 とそんなわけで、ネタばらし。簡単に言ってしまうと<a href=\"http://www.google.com/\">Google</a>のキャッシュ機能と似たようなもので、自動的に巡回してはそのサイトの中身をごっそり抜き取り転載・保存してしまうと言う実に興味深いサイトです。『パクリ禁止！』とか『持ち帰り禁止！』等と抜かしているサイト管理人がこれを見たら涙流して喜びそうですね。 はい、では私のサイトの時間旅行へとご案内いたします。私のこさえた過去の異物…ではなかった、遺物をとくとごらんあれ。(もっと古いものもあるかと思ったが、やっぱ当時マイナーだったから巡回すらされなかったのかな……)</p>\n<ul>\n<li><a href=\"http://web.archive.org/web/20011107131140/http://www.geocities.co.jp/Playtown-Domino/3933/\">DECLINED BBS RINGS</a>(2001/6) 昔やってた寂れたサイト同士のRing。自動登録ではなく単なるリンク集という説もあり。</li>\n<li><a href=\"http://web.archive.org/web/20010606205909/www6.org1.com/%7Emaki/\">Vanished YASiM-CITY</a>(2001/6当時) Vanished YASiM-CITYとしては最も(？)繁栄した時期。</li>\n</ul>","frontmatter":{"cover":null,"date":"2002-09-30T00:00:00+09:00","strDate":"2002/9/29","title":"WWW内で時間旅行"}}},"pathContext":{"path":"/ja/blog/2002/09/30-www内で時間旅行/","langKey":"ja"}}

